@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Fjalla+One&family=Press+Start+2P&display=swap);

* {
  font-family: "Fjalla One", sans-serif;
  /* font-size: 15px; */
  /* color: azure; */
}

body {
  color: rgb(17, 17, 17);
}

.nav {
  position: fixed;
  top: 0px;
  width: 100vw;

  background-color: black;
  color: white;
  font-size: 30px;
  padding: 20px;
  z-index: 1000;
  align-items: center;

  box-shadow: -3px 10px 26px 0px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: -3px 10px 26px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: -3px 10px 26px 0px rgba(0, 0, 0, 0.75);

  display: flex;
  justify-content: space-between;
}

.heading {
  text-shadow: 1px 3px 3px rgba(0, 0, 0, 0.6);

  color: rgb(0, 0, 0);

  font-family: "Bebas Neue", sans-serif;
  font-size: 30px;
}

body {
  background-image: url(/static/media/pexels-panumas-nikhomkhai-1148820.4936ff5c.jpg);
  background-size: cover;
}

.title {
  font-family: "Press Start 2P", sans-serif;
  text-align: center;
  position: absolute;
  font-size: 1.5rem;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.git {
  font-size: 15px;
}

.buttons {
  background-color: transparent;
  color: white;
  border: none;
  /* font-size: 20px; */
  text-decoration: none;
  cursor: pointer;
}

@media screen and (max-width: 900px) {
  .git {
    display: none;
  }
  .title {
    font-size: 18px;
  }
}

.widget-container {
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.753);
  margin: 20px;
  border-radius: 10px;
}

.widget-container > h1 {
  /* color: white; */
  text-align: center;
  font-size: 30px;
  text-shadow: 1px 3px 3px rgba(0, 0, 0, 0.6);
  font-family: "Press Start 2P", sans-serif;

  /* background-color: aliceblue; */
}

.bold {
  font-weight: bold;
}

.widget {
  /* color: rgb(158, 158, 158); */
  margin-bottom: 25px;
  /* taken from https://css-tricks.com/examples/GradientBorder/ */
  /* width: ; */
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr 1.2fr;
  grid-gap: 10%;
  gap: 10%;
  justify-content: space-around;
}
.not-active {
  text-shadow: 4px 7px 11px rgba(0, 0, 0, 0.6);

  display: flex;
  align-items: center;
  /* justify-content: space-around;te */
  font-family: "Press Start 2P", sans-serif;
  color: #8b0000;
  font-size: 80px;
  position: absolute;
  z-index: 2;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.cpu-wdiget {
  margin-top: 20px;
}

.cpu,
.mem {
  background-color: white;
  position: relative;
  text-align: center;
  background-color: rgba(180, 180, 180, 0.411);
  padding: 10px;
  border-radius: 10px;
}

.cpu-info {
  background-color: rgba(180, 180, 180, 0.411);
  padding: 10px;
  border-radius: 10px;
  padding: 10px 30px;
}

.cpu-text,
.mem-text {
  position: absolute;
  -webkit-transform: translate(-45%, -50%);
          transform: translate(-45%, -50%);
  top: 50%;
  color: rgb(168, 168, 168);
  left: 50%;
  text-shadow: 4px 7px 11px rgba(0, 0, 0, 0.6);

  font-size: 30px;
}

.canvas-wrapper {
  position: relative;
}

@media screen and (max-width: 900px) {
  .widget {
    display: block;
  }

  .cpu,
  .mem,
  .cpu-info {
    margin: 20px 0px;
    padding: 10%;
  }

  .not-active {
    font-size: 40px;
    top: 20px;
  }
}

