.widget-container {
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.753);
  margin: 20px;
  border-radius: 10px;
}

.widget-container > h1 {
  /* color: white; */
  text-align: center;
  font-size: 30px;
  text-shadow: 1px 3px 3px rgba(0, 0, 0, 0.6);
  font-family: "Press Start 2P", sans-serif;

  /* background-color: aliceblue; */
}

.bold {
  font-weight: bold;
}

.widget {
  /* color: rgb(158, 158, 158); */
  margin-bottom: 25px;
  /* taken from https://css-tricks.com/examples/GradientBorder/ */
  /* width: ; */
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr 1.2fr;
  gap: 10%;
  justify-content: space-around;
}
.not-active {
  text-shadow: 4px 7px 11px rgba(0, 0, 0, 0.6);

  display: flex;
  align-items: center;
  /* justify-content: space-around;te */
  font-family: "Press Start 2P", sans-serif;
  color: #8b0000;
  font-size: 80px;
  position: absolute;
  z-index: 2;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.cpu-wdiget {
  margin-top: 20px;
}

.cpu,
.mem {
  background-color: white;
  position: relative;
  text-align: center;
  background-color: rgba(180, 180, 180, 0.411);
  padding: 10px;
  border-radius: 10px;
}

.cpu-info {
  background-color: rgba(180, 180, 180, 0.411);
  padding: 10px;
  border-radius: 10px;
  padding: 10px 30px;
}

.cpu-text,
.mem-text {
  position: absolute;
  transform: translate(-45%, -50%);
  top: 50%;
  color: rgb(168, 168, 168);
  left: 50%;
  text-shadow: 4px 7px 11px rgba(0, 0, 0, 0.6);

  font-size: 30px;
}

.canvas-wrapper {
  position: relative;
}

@media screen and (max-width: 900px) {
  .widget {
    display: block;
  }

  .cpu,
  .mem,
  .cpu-info {
    margin: 20px 0px;
    padding: 10%;
  }

  .not-active {
    font-size: 40px;
    top: 20px;
  }
}
