@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Fjalla+One&family=Press+Start+2P&display=swap");

* {
  font-family: "Fjalla One", sans-serif;
  /* font-size: 15px; */
  /* color: azure; */
}

body {
  color: rgb(17, 17, 17);
}

.nav {
  position: fixed;
  top: 0px;
  width: 100vw;

  background-color: black;
  color: white;
  font-size: 30px;
  padding: 20px;
  z-index: 1000;
  align-items: center;

  box-shadow: -3px 10px 26px 0px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: -3px 10px 26px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: -3px 10px 26px 0px rgba(0, 0, 0, 0.75);

  display: flex;
  justify-content: space-between;
}

.heading {
  text-shadow: 1px 3px 3px rgba(0, 0, 0, 0.6);

  color: rgb(0, 0, 0);

  font-family: "Bebas Neue", sans-serif;
  font-size: 30px;
}

body {
  background-image: url("./pexels-panumas-nikhomkhai-1148820.jpg");
  background-size: cover;
}

.title {
  font-family: "Press Start 2P", sans-serif;
  text-align: center;
  position: absolute;
  font-size: 1.5rem;
  left: 50%;
  transform: translateX(-50%);
}

.git {
  font-size: 15px;
}

.buttons {
  background-color: transparent;
  color: white;
  border: none;
  /* font-size: 20px; */
  text-decoration: none;
  cursor: pointer;
}

@media screen and (max-width: 900px) {
  .git {
    display: none;
  }
  .title {
    font-size: 18px;
  }
}
